<template>
	<view class="login-container">
		<!-- ÁôªÂΩïÂÜÖÂÆπÂå∫ -->
		<view class="login-content">
			<!-- Ê†áÈ¢òÂå∫Âüü -->
			<view class="title-area">
				<text class="welcome">Ê¨¢ËøéÁôªÂΩï</text>
				<text class="subtitle">ÊÇ®Â•ΩÔºåÊ¨¢ËøéÁôªÂΩïÁîµÂä®ËΩ¶‰æõÈÖç‰øÆÊúçÂä°</text>
			</view>
			
			<!-- Ë°®ÂçïÂå∫Âüü -->
			<view class="form-area">
				<!-- ÊâãÊú∫Âè∑ËæìÂÖ•Ê°Ü -->
				<view class="input-box">
					<text class="iconfont">üì±</text>
					<input 
						class="custom-input"
						type="number" 
						v-model="phone" 
						placeholder="ËØ∑ËæìÂÖ•ÊâãÊú∫Âè∑Á†Å" 
						maxlength="11"
					/>
				</view>
				
				<!-- ÂØÜÁ†ÅËæìÂÖ•Ê°Ü -->
				<view class="input-box">
					<text class="iconfont">üîí</text>
					<input 
						class="custom-input"
						:type="showPassword ? 'text' : 'password'" 
						v-model="password" 
						placeholder="ËØ∑ËæìÂÖ•ÂØÜÁ†Å"
						:hold-keyboard="true"
						:adjust-position="false"
					/>
					<view class="eye-box" @click="togglePasswordVisibility">
						<text class="iconfont eye">üëÅÔ∏è</text>
					</view>
				</view>
				
				<!-- Ê≥®ÂÜåÂÖ•Âè£ -->
				<view class="register-link">
					<text @click="goToRegister">Ê≥®ÂÜåË¥¶Âè∑</text>
				</view>
				
				<!-- ÁôªÂΩïÊåâÈíÆ -->
				<button class="login-btn" @click="handleLogin">ÁôªÂΩï</button>
				
				<!-- È™åËØÅÁ†ÅÁôªÂΩïÂÖ•Âè£ -->
				<view class="verify-login">
					<text @click="switchToVerifyLogin">È™åËØÅÁ†ÅÁôªÂΩï</text>
				</view>
			</view>
			
			<!-- Â∫ïÈÉ®ÂçèËÆÆ -->
			<view class="agreement">
				<text class="agreement-text">
					ÁôªÂΩï‰ª£Ë°®ÊÇ®Â∑≤ÁªèÂêåÊÑè
					<text class="link">„ÄäÁî®Êà∑ÂçèËÆÆ„Äã</text>
					Âíå
					<text class="link">„ÄäÈöêÁßÅÊùÉÊîøÁ≠ñ„Äã</text>
				</text>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				phone: '',
				password: '',
				showPassword: false,
				nickName: '',
				avatarUrl: '',
				code: ''
			}
		},
		onLoad(options) {
			// Ëé∑ÂèñÈ°µÈù¢‰º†ÈÄíÁöÑÂèÇÊï∞
			if (options.code) {
				this.code = options.code
			}
			if (options.nickName) {
				this.nickName = decodeURIComponent(options.nickName)
			}
			if (options.avatarUrl) {
				this.avatarUrl = decodeURIComponent(options.avatarUrl)
			}
		},
		methods: {
			togglePasswordVisibility() {
				this.showPassword = !this.showPassword
			},
			handleLogin() {
				if (!this.phone || !this.password) {
					uni.showToast({
						title: 'ËØ∑ËæìÂÖ•ÊâãÊú∫Âè∑ÂíåÂØÜÁ†Å',
						icon: 'none'
					})
					return
				}
				
				// ËøôÈáåÂ∫îËØ•Ë∞ÉÁî®ÂêéÁ´ØÊé•Âè£ÔºåÂèëÈÄÅ code Ëé∑Âèñ openid
				// Á§∫‰æãËØ∑Ê±Ç:
				uni.request({
					url: 'YOUR_API_URL/login',
					method: 'POST',
					data: {
						code: this.code,
						phone: this.phone,
						password: this.password,
						nickName: this.nickName,
						avatarUrl: this.avatarUrl
					},
					success: (res) => {
						if (res.data.success) {
							// ‰øùÂ≠òÁî®Êà∑‰ø°ÊÅØÂíåtoken
							const userInfo = {
								name: this.nickName || 'Áî®Êà∑',
								phone: this.phone.replace(/(\d{3})\d{4}(\d{4})/, '$1****$2'),
								avatar: this.avatarUrl || '/static/my/default-avatar.png',
								openid: res.data.openid, // ‰øùÂ≠òopenid
								token: res.data.token // ‰øùÂ≠òtoken
							}
							
							uni.setStorageSync('userInfo', JSON.stringify(userInfo))
							uni.setStorageSync('token', res.data.token)
							
							uni.showToast({
								title: 'ÁôªÂΩïÊàêÂäü',
								icon: 'success',
								duration: 1500,
								success: () => {
									setTimeout(() => {
										uni.navigateBack({
											delta: 1
										})
									}, 1500)
								}
							})
						} else {
							uni.showToast({
								title: res.data.message || 'ÁôªÂΩïÂ§±Ë¥•',
								icon: 'none'
							})
						}
					},
					fail: (err) => {
						console.log('ÁôªÂΩïÂ§±Ë¥•', err)
						uni.showToast({
							title: 'ÁôªÂΩïÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
							icon: 'none'
						})
					}
				})
			},
			switchToVerifyLogin() {
				uni.navigateTo({
					url: '/pages/login/verify'
				})
			},
			goToRegister() {
				uni.navigateTo({
					url: '/pages/register/register'
				})
			}
		}
	}
</script>

